import React from 'react';
import './sidebar.scss';
import {
  Route,
  NavLink,
  HashRouter
} from "react-router-dom";
import Home from "../Home/home";
import About from "../about/About";
import Skills from "../Skill/Skills";
import Degree from "../Degree/degree";
import Work from "../work/Work";

export default class Sidebar extends React.Component{
 render(){
  return(
  	<HashRouter>
			<div>
				<div className="sidenav">
          <NavLink className="aboutButton" exact to="/">Home</NavLink>
          <NavLink id="test" to="/about">About Me</NavLink>
					<NavLink to="/skills">My Skills</NavLink>
					<NavLink to="/portfolio">My Works</NavLink>
					<NavLink to="/resume">My Degree</NavLink>
				</div>
				<div className="hide"></div>
				<div className="content">
          <Route className="test" exact path="/" component={Home}/>
          <Route path="/about" component={About}/>
          <Route path="/skills" component={Skills}/>
          <Route path="/portfolio" component={Work}/>
          <Route path="/resume" component={Degree}/>
        </div>
			</div>
		</HashRouter>
  );
 }
}